.raceInfo .container {
    max-width: 800px;
    background-color: #fff;
    margin: 5% auto;
    padding: 20px 0;
    border: 10px solid #d4faf0;
    outline: 5px solid #33cc99;
}

.raceInfo img {
    display: inline-block;
    width: 3%;
}

.bikeAnimation {
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #257e9f;
}

.bikeAnimation i {
    font-size: 200px;
    color: white;
    -webkit-animation: bikeMoving;
            animation: bikeMoving;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.bikeAnimation p {
    font-size: 3rem;
    text-align: center;
    max-width: 500px;
    color: white;
}

@-webkit-keyframes bikeMoving {
    0% {
        -webkit-transform: translateX(-1000px);
                transform: translateX(-1000px);
    }
    100% {
        -webkit-transform: translateX(1000px);
                transform: translateX(1000px);
        display: none;
    }
}

@keyframes bikeMoving {
    0% {
        -webkit-transform: translateX(-1000px);
                transform: translateX(-1000px);
    }
    100% {
        -webkit-transform: translateX(1000px);
                transform: translateX(1000px);
        display: none;
    }
}

